import React, { useState, useEffect } from 'react';
import {useDispatch} from 'react-redux';
import { add } from '../actions'
import '.././tailwind.css'
import { Link } from 'react-router-dom';
import api from './api/api';

function Set() {
    const [ list , setlist ] = useState();
    const [ name , setname ] = useState();

    const dispatch = useDispatch();
    useEffect(() => {
      api('/users').then(response => setlist(response.data));
    })

    const onChangename = e => {
      setname(e.target.value);
    };
    const check = e => {
      let exist = false;
      
      for(let i = 0;i<list.length;i++) {
        list[i]["이름"] == name ? exist = true : console.log(false);
      }

      if(exist) {
        dispatch(add(name))
      } else {
        e.preventDefault();
        alert("이름을 다시 입력해주세요.")
      }
  }
  return (
    <div class="h-screen bg-white-100 pt-8 items-center">
            <p style={{display:"block"}} class="text-center mx-auto pb-4">이름을 입력해주세요</p>
            <input style={{display:"block"}} name="target" class="py-auto mx-auto text-3xl py-2 w-5/6 border-2 text-center" type="text" placeholder="홍길동" onChange={onChangename} />
            <Link style={{display:"block"}} class="mt-4 bg-gray-200 w-3/4 mx-auto text-center py-2" to="/" onClick={check}>이름 등록하기</Link> 
    </div>
  );
}

export default Set;
