import React from 'react';
import ReactDOM from 'react-dom';
import {BrowserRouter  , Route } from 'react-router-dom';
import * as serviceWorker from './serviceWorker';

import { Provider, useSelector } from 'react-redux';
import allReducers from './reducers';
import { createStore } from 'redux';


import {Tab,Nav} from './views/components'
import {Main,Set,Money,Pay,Manage,Rank,Etc,Exam} from './views/';
import './index.css'
import './tailwind.css'

const store = createStore(allReducers,window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__());
function App() {
    const set = useSelector(state => state.set);
    const name = JSON.stringify(set[0]);
    let check = name !== undefined ? true : false;
    return(
        <BrowserRouter>
            <div class="max-w-screen-sm mx-auto">
                <Nav/>
                {check ? <Route exact path="/" component={Main} /> : <Route path="/" component={Set} />}
                {check && <Route path="/money_add" component={Money} />}
                {check && <Route path="/pay" component={Pay} />}
                {check && <Route path="/manage" component={Manage} />}
                {check && <Route path="/rank" component={Rank} />}
                {check && <Route path="/etc" component={Etc} />}
                {check && <Route path="/exam" component={Exam} />}
                <Tab />
            </div>
        </BrowserRouter>
    )
}
ReactDOM.render( <Provider store={store}><App /></Provider>, document.getElementById('root'));
serviceWorker.unregister();
