import React from 'react';
import '../tailwind.css';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import InboxIcon from '@material-ui/icons/Inbox';
import DraftsIcon from '@material-ui/icons/Drafts';
import { Link } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 640,
    backgroundColor: theme.palette.background.paper,
  },
}));

function ListItemLink(props) {
  return <ListItem button component="a" {...props} />;
}

function SimpleList() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <List component="nav" aria-label="main mailbox folders">
      <Link to="/manage"><ListItem button>
            <ListItemIcon>
                <img class="h-8" src="https://image.flaticon.com/icons/svg/2503/2503371.svg"></img>
            </ListItemIcon>
            <ListItemText primary="Money Management"/>
        </ListItem></Link>
        <ListItem button>
          <ListItemIcon>
            <img class="h-8" src="https://image.flaticon.com/icons/svg/2289/2289375.svg"></img>
          </ListItemIcon>
          <ListItemText primary="Money view" />
        </ListItem>
        <Link to="/manage_list"><ListItem button>
            <ListItemIcon>
                <img class="h-8" src="https://image.flaticon.com/icons/svg/2503/2503371.svg"></img>
            </ListItemIcon>
            <ListItemText primary="표로 보기"/>
        </ListItem></Link>
      </List>
      <Divider />
    </div>
  );
}

function etc() {
    return (
        <div class="w-full">
          <SimpleList />
        </div>
    )
}

export default etc